<template>
  <div class="home">
    <img alt="Vue logo" src="logo.png" />
    <!-- <HelloWorld msg="Welcome to Your Vue.js App" /> -->
    <button class="btn-base bg-purple" @click="walletLogin('GitHub')">
      <span>Github </span>
    </button>
    <button class="btn-base bg-purple" @click="walletLogin('Twitter')">
      <span>Twitter </span>
    </button>
    <button class="btn-base bg-purple" @click="walletLogin('Metamask')">
      <span>Metamask </span>
    </button>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";

export default {
  name: "home",
  // components: {
  //   HelloWorld
  // },
  methods: {
    //  
    async walletLogin(type) {
      if (type === "GitHub") {
        this.$router.push({ name: "GithubLogin" });
        return;
      } else if (type === "Twitter") {
        this.$router.push({ name: "TwitterLogin" });
        return;
      }
    }
  }
};
</script>
